.header {
  background: rgba(0, 0, 0, 0);
  transition: all 0.3s ease;
  z-index: 1;
  top: 0;
  left: 0;
  position: fixed;
}

.top {
  top: 0px;
}

.down {
  top: -90px;
}

.up {
  top: 0px;
}

.up,
.down {
  background: #06747d;
  box-shadow: 0px 0px 5px black;
  padding: 8px;
}
