/* Scrollbar */
/* https://www.youtube.com/watch?v=wMmcwl78gEk */
::-webkit-scrollbar {
  width: 8px;
  // width: 0px;
}

::-webkit-scrollbar-track {
  background: rgba(10, 37, 59, 0.85);
}

::-webkit-scrollbar-thumb {
  background: rgba(172, 172, 172, 0.35);
  border-radius: 25px;
}

html {
  // height: 100%;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

