.carousel-container {
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // height: 100vh;
}

.swiper-button-next,
.swiper-button-prev {
  color: white !important;
}

.swiper-pagination-bullet,
.swiper-pagination-bullet-active {
  background-color: white !important;
}

.swiper-wrapper {
  display: flex;
  align-items: center;
}

.swipercontainer {
  width: 22rem;
  // min-height: 20rem;
  // width: 34rem;
  // height: auto;

  img {
    cursor: pointer;
    width: 100%;
    height: 100%;
    // object-fit: contain;
  }

  // video {
  //   max-width: 100%;
  //   max-height: 100%;
  //   object-fit: contain;
  // }

  .img-container {
    display: flex;
    // border: 1px solid white;
    justify-content: center;

    .play-button {
      display: flex;
      margin: auto;
      position: absolute;
      text-align: center;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      z-index: 10;
      width: 6rem;
      height: 6rem;
      // object-fit: scale-down;
      cursor: pointer;
    }

    circle {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }

    svg {
      // filter: brightness(0.8);
      opacity: 0.6;
      transition: opacity 0.3s ease-in-out;
      -moz-transition: opacity 0.3s ease-in-out;
      -webkit-transition: opacity 0.3s ease-in-out;
    }

    svg:hover {
      // filter: brightness(1);
      opacity: 1;
      transition: opacity 0.3s ease-in-out;
      -moz-transition: opacity 0.3s ease-in-out;
      -webkit-transition: opacity 0.3s ease-in-out;
    }

    .shadow {
      -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
      filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    }
  }

  .myswiper {
    width: 100%;
    height: 100%;

    .swiperslide {
      img {
        pointer-events: auto;
        width: 100%;
        height: 100%;

        // object-fit: cover;
      }
    }
  }
}

@media (max-width: 40em) {
  .swipercontainer {
    width: 18rem;
  }
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(27, 27, 27, 0.541);
  display: flex;
  align-items: center;
  animation: fadein 1s;
  z-index: 999;

  img {
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  }

  video {
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  }

  // .dismiss {
  //   position: absolute;
  //   right: 0;
  //   top: 0;
  //   cursor: pointer;
  //   z-index: 999;
  // }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
